body {
  margin: 0;
  padding: 10px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  margin: 0;
  font-size: 1.5em;
}

nav ul {
  padding: 0;
  list-style: none;
}

nav ul li {
  display: inline-block;
}
nav ul a {
  display: inline-block;
  padding: 8px;
  text-decoration: none;
  color: black;
  font-weight: bold;
  background: antiquewhite;
  margin: 5px;
}

p#debug::after {
  content: 'homescreen';
}
@media (display-mode: standalone) {
  p#debug::after {
    content: 'standalone pda';
  }

}

  h2 {
      margin: 0;
      color: #382;
      font-size: 1.3em;  
  }
  h3 {
      color: #823;
      margin-bottom: 5px;
      font-size: 1.1em;
  }
  ul#cityList {
    padding: 0;
    list-style: none;
  }
  button.delete {
      border: 0;
  }
  ul#cityList a {
    display: inline-block;
    padding: 8px;
    text-decoration: none;
    color: white;
    background-color: #382;
    margin: 5px;
    border-radius: 10px 0;
  }
h2#city {
    text-align: center;
}

#temperature {
    text-align: center;
    font-size: 3em;    
    margin: 5px;
}
